* {
  margin: 0;
  padding: 0;
  font-family: Calibri, sans-serif;
}

.wrapper {
  text-align: center;
  height: 100vh;
  line-height: 30px;
  color: #000101;
  background-color: rgb(253, 255, 255);
}

@media (max-width: 768px) {
  .wrapper {
    font-size: 14px;
    padding: 20px;
  }
}
